import React, { useState, useEffect } from "react";

// reactstrap components
import {
  Button,
  ButtonGroup,
  Card,
  CardHeader,
  CardBody,
  CardTitle,
  FormGroup,
  Form,
  Input,
  Row,
  Col,
  Label,
  Table,
  UncontrolledTooltip,
  Modal,
} from "reactstrap";
// react component used to create sweet alerts
import classNames from "classnames";
import io from "socket.io-client";
import { connect } from "react-redux";
import ReactDatetime from "react-datetime";
import Select from "react-select";
import ReactTable from "components/ReactTable/ReactTable.js";
import "./sniper.css";
import NotificationAlert from "react-notification-alert";
const UniSwapOneToken = (props) => {
  const {apiConfig, ApiCall} = global;
  const [socket, setSocket] = useState("");
  const notificationAlertRef = React.useRef(null);
  const [wallets, setWallets] = useState([]);
  const [walletData, setWalletData] = useState([]);
  //Modal
  const [modalAdd, setModalAdd] = useState(false);
  const [addData, setAddData] = useState({
    private: '', //
    public: '', //
    gasPricePlus: 10, // gwei
    gasPriceMinus: 10, // number
    tokenPairs: '', //
    minLimit: 0,
    status: 0,
    enableFixAmount: 'disable',
    fixedAmount: 0
  });
  const [detailModalStatus, setDetailModalStatus] = useState(false);
  const [detailData, setDetailData] = useState(false);
  const toggleModalAdd = () => {
    setModalAdd(!modalAdd);
  };
  const showDetailModal = (data) => {
    setDetailModalStatus(true);
    setDetailData(JSON.parse(JSON.stringify(data)));
  };
  const closeDetailModal = () => {
    setDetailModalStatus(false);
  }
  //Modal-end
  //set table
  const [logData, setLogData] = useState([]);
  const itemRows = [];
  for (let item of logData) {
    const row = (
      <tr key={item.bTx}>
        <td key={1} className="text-center">
          <div className="photo">
            <img
              alt="..."
              src={require("assets/img/bitcoin.png").default}
            />
          </div>
        </td>
        <td key={2}><a href={`https://bscscan.com/address/${item.fromToken}`} target="_blank">{item.fromToken}</a></td>
        <td key={3}><a href={`https://bscscan.com/address/${item.toToken}`} target="_blank">{item.toToken}</a></td>
        <td key={4} className="text-left">{item.amountIn}</td>
        <td key={5} className="text-left">{item.amountOut}</td>
        <td key={6} className="text-left">{item.typeName}</td>
        <td key={7} className="text-left">{item.txStatus}</td>
        <td key={8} className="text-left">{item.created}</td>
        <td key={9} className="text-left">
          <Button className="btn-link btn-icon" color="success" size="sm" onClick={() => showDetailModal(item)}>
            <i className="tim-icons icon-bullet-list-67" />
          </Button>
          <Button className="btn-link" color="danger" size="sm"  onClick={() => del(item._id)} title="Delete">
            <i className="tim-icons icon-simple-remove" />
          </Button>
        </td>
      </tr>
    );
    itemRows.push(row);
  }
  useEffect(() => {
    if (props.credential.loginToken) {
      setSocket(
        io(apiConfig.endPoint, {
          auth: {
            token: props.credential.loginToken,
          },
        })
      );
    } else {
      setSocket(io(apiConfig.endPoint));
    }
  }, [props.credential.loginToken]);
  useEffect(() => {
    if (socket) {
      socket.on("connect", () => {
        // when connection started
        console.log("connect");
        socket.emit("uniswap:one:getplan", (response) => {
          if (response) {
            if (response.plan) setAddData(response.plan);
            const arr = [];
            for (let i = 0; i < response.wallet.length; i++) {
              arr.push(
                {
                  value: i,
                  label: `${response.wallet[i].public},${response.wallet[i].private}`,
                }
              );
            }
            setWallets(response.wallet);
            setWalletData(arr);
          }
        });
        socket.emit("uniswap:one:getLogs", (response) => {
          console.log(response);
          setLogData(response);
        });
        socket.on("uniswap:one:newPlan", (data) => {
          console.log('planUpdated');
          if(data){
            setAddData(data);
          }
        });
        socket.on("uniswap:one:logStatus", (data) => {
          setLogData(data);
        });
        socket.on("uniswap:one:newTokenBought", (data) => {
          console.log('Bought')
          showNotify('New Bought.', `Hash:${data}`)
        });
        socket.on("uniswap:one:tokenMoved", (data) => {
          console.log('Moved')
          showNotify('Moved to main pool', `Hash:${data}`)
        });
        socket.on("uniswap:one:tokenSelled", (data) => {
          console.log('selled')
          showNotify('Token selled', `Hash:${data}`)
        });
      });
    }
    return () => {
      if (socket) socket.disconnect();
    };
  }, [socket]);
  const showNotify = async (title, text, type = "success", place = 'tr', autoDismiss = 5) => {
    const options = {
      place: place,
      message: (
        <div><div>{title}</div><div>{text}</div></div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: autoDismiss,
    };
    notificationAlertRef.current.notificationAlert(options);
  }
  const addHandler = async () => {
    socket.emit("uniswap:one:setPlan", addData, (response) => {
      setAddData(response.data);
      setModalAdd(!modalAdd);
    });
  };
  const startStopHandler = async (status) => {
    addData.status = status;
    socket.emit("uniswap:one:setPlan", addData, (response) => {
      setAddData(response.data);
    });
  };
  const sell = async (hash) => {
    socket.emit("uniswap:one:letSell", hash, (response) => {
      if (response.code === 1) {
        setLogData(response.data);
        showNotify(response.msg);
      } else {
        showNotify(response.msg, '', 'danger');
      }
    });
  };
  const del = async (id) => {
    socket.emit("uniswap:one:letDel", id, (response) => {
      if (response.code === 1) {
        setLogData(response.data);
        showNotify(response.msg);
      } else {
        showNotify(response.msg, '', 'danger');
      }
    });
  };
  const delAll = async () => {
    socket.emit("uniswap:one:letDelAll", (response) => {
      if (response.code === 1) {
        setLogData(response.data);
        showNotify(response.msg);
      } else {
        showNotify(response.msg, '', 'danger');
      }
    });
  };
  return (
    <>
      <div className="rna-container">
        <NotificationAlert ref={notificationAlertRef} />
      </div>
      <div className="content">
        {/* info and setting bar */}
        <Card>
          <Row>
            <Col md="3">
              <CardBody>
                <Button
                  color="danger"
                  className="ml-auto mr-auto"
                  onClick={() => setModalAdd(true)}
                >
                  Setting
                </Button>
              </CardBody>
            </Col>
            <Col md="6">
              <CardBody>
                <div className="infoDiv">
                  {
                    addData && addData._id &&
                    <>
                      <div className="infoP">TokenPairs: {addData.tokenPairs}</div>
                    </>
                  }
                  {
                    addData && !addData._id &&
                    <>
                      <div>Please make setting</div>
                    </>
                  }
                </div>
              </CardBody>
            </Col>
            <Col md="3">
              <CardBody>
                {
                  addData && addData._id &&
                  <Button
                    color={addData.status === 1 ? 'danger' : 'success'}
                    className="ml-auto mr-auto"
                    onClick={() => startStopHandler(addData.status === 1 ? 0 : 1)}
                  >
                    {addData.status === 1 ? 'Stop' : 'Start'}
                  </Button>
                }
              </CardBody>
            </Col>
          </Row>

        </Card>
        {/* logs table */}
        <Card>
          <Col xs={12} md={12}>
            <Card>
              <CardHeader>
                <CardTitle tag="h4">Swap token logs</CardTitle>
                <Button className="btn-link float-right" color="danger" size="sm"  onClick={() => delAll()} title="Delete All">
                  Remove All
                </Button>
              </CardHeader>
              <CardBody>
                <Table responsive>
                  <thead className="text-primary">
                    <tr>
                      <th className="text-center">#</th>
                      <th>Token From</th>
                      <th className="text-left">Token To</th>
                      <th className="text-left">Amount In</th>
                      <th className="text-left">Amount Out</th>
                      <th className="text-left">Type</th>
                      <th className="text-left">Status</th>
                      <th className="text-left">CreatedAt</th>
                      <th className="text-left">Actions</th>
                    </tr>
                  </thead>
                  <tbody>
                    {itemRows}
                  </tbody>
                </Table>
              </CardBody>
            </Card>
          </Col>
        </Card>
      </div>
      {/* setting modal */}
      <Modal modalClassName="modal-black mModal mm1" isOpen={modalAdd}>
        <div className="modal-header">
          <h4>Set plan for front-run.</h4>
          <button
            aria-label="Close"
            className="close"
            data-dismiss="modal"
            type="button"
            onClick={toggleModalAdd}
          >
            <i className="tim-icons icon-simple-remove" />
          </button>
        </div>
        <div className="modal-body">
          <Form>
            <Row>
              <Col className="pr-md-1" md="12">
                <FormGroup>
                  <label>Token Pairs(Pair token by colon, token pairs by comma. Please input correctly.)</label>
                  <Input
                    type="text"
                    placeholder="token1:token2,token3:token4"
                    value={addData.tokenPairs}
                    onChange={(e) =>
                      setAddData({ ...addData, tokenPairs: e.target.value })
                    }
                  />
                </FormGroup>
              </Col>
              <Col className="pr-md-1" md="12">
                <FormGroup>
                  <label>Select Wallet</label>
                  <Select
                    options={walletData}
                    placeholder="Select wallet"
                    className="react-select info"
                    classNamePrefix="react-select"
                    onChange={(e) => {
                      setAddData({ ...addData, public: wallets[e.value].public, private: wallets[e.value].private })
                    }}
                  >
                  </Select>
                </FormGroup>
              </Col>
              <Col className="pr-md-1" md="12">
                <Row>
                  <Col className="pr-md-1" md="6">
                    <FormGroup>
                      <label>Public Key</label>
                      <Input
                        type="text"
                        value={addData.public}
                        readOnly
                      />
                    </FormGroup>
                  </Col>
                  <Col className="pr-md-1" md="6">
                    <FormGroup>
                      <label>Priate Key</label>
                      <Input
                        type="password"
                        value={addData.private}
                        readOnly
                      />
                    </FormGroup>
                  </Col>
                </Row>
              </Col>
              <Col className="pr-md-1" md="12">
                <Row>
                  <Col className="pr-md-1" md="6">
                    <FormGroup>
                      <label>Gas price plus(in gwei, &gt; 0)</label>
                      <Input
                        type="number"
                        value={addData.gasPricePlus}
                        onChange={(e) =>
                          setAddData({ ...addData, gasPricePlus: e.target.value })
                        }
                      />
                    </FormGroup>
                  </Col>
                  <Col className="pr-md-1" md="6">
                    <FormGroup>
                      <label>Gas price minus(in gwei, &gt; 0)</label>
                      <Input
                        type="number"
                        value={addData.gasPriceMinus}
                        onChange={(e) =>
                          setAddData({ ...addData, gasPriceMinus: e.target.value })
                        }
                      />
                    </FormGroup>
                  </Col>
                </Row>
              </Col>
              <Col className="pr-md-1" md="12">
                  <FormGroup>
                    <label>Transaction amount / Liquidity(Min Limit, %)</label>
                    <Input
                      type="number"
                      value={addData.minLimit}
                      onChange={(e) =>
                        setAddData({ ...addData, minLimit: e.target.value })
                      }
                    />
                  </FormGroup>
              </Col>
              <Col className="pr-md-1" md="12">
                <Row>
                  <Col className="pr-md-1" md="6">
                    <Row >
                      <Col className="pr-md-1" md="6">
                        <label className="pr-md-1">Set the buy amount</label>
                      </Col>
                    </Row>
                    <Row>
                      <Col className="pr-md-1" md="6">
                        <FormGroup check className="form-check-radio">
                          <Label check>
                            <Input
                              checked={addData.enableFixAmount === "enable"}
                              value="enable"
                              id="enableFixAmountInput"
                              name="fixAmount"
                              type="radio"
                              onChange={(e) =>
                                setAddData({ ...addData, enableFixAmount: e.target.value })
                              }
                            />
                            <span className="form-check-sign" />
                            Enable
                          </Label>
                        </FormGroup>
                      </Col>
                      <Col className="pr-md-1" md="6">
                        <FormGroup check className="form-check-radio">
                          <Label check>
                            <Input
                              checked={addData.enableFixAmount === "disable"}
                              value="disable"
                              id="disableFixAmountInput"
                              name="fixAmount"
                              type="radio"
                              onChange={(e) =>
                                setAddData({ ...addData, enableFixAmount: e.target.value })
                              }
                            />
                            <span className="form-check-sign" />
                            Disable
                          </Label>
                        </FormGroup>
                      </Col>
                    </Row>
                  </Col>
                  <Col className="pr-md-1" md="6">
                    {
                      addData.enableFixAmount === "enable" &&
                      <FormGroup>
                        <label>Fixed Amount</label>
                        <Input
                          type="number"
                          value={addData.fixedAmount}
                          onChange={(e) =>
                            setAddData({ ...addData, fixedAmount: e.target.value })
                          }
                        />
                      </FormGroup>
                    }
                  </Col>
                </Row>
              </Col>
              <Col className="pr-md-1" md="12">
                <Button color="danger" size="lg" onClick={addHandler}>
                  O K
                </Button>
              </Col>
            </Row>
          </Form>
        </div>
      </Modal>
      {/* detail modal */}
      <Modal modalClassName="modal-black mModal" isOpen={detailModalStatus}>
        <div className="modal-header">
          <h4>Information</h4>
          <button
            aria-label="Close"
            className="close"
            data-dismiss="modal"
            type="button"
            onClick={closeDetailModal}
          >
            <i className="tim-icons icon-simple-remove" />
          </button>
        </div>
        {
          detailData && 
          <div className="modal-body padBtt">
            <Row>
              <Col className="pr-md-1" md="6">
                <Row>
                  <Col className="pr-md-1" md="12">
                    <FormGroup>
                      <label>FromTokenName</label>
                      <div className="labelOne">{detailData.fromToken}</div>
                    </FormGroup>
                  </Col>
                  <Col className="pr-md-1" md="12">
                    <FormGroup>
                      <label>ToTokenName</label>
                      <div className="labelOne">{detailData.toToken}</div>
                    </FormGroup>
                  </Col>
                  <Col className="pr-md-1" md="12">
                    <FormGroup>
                      <label>AmountIn</label>
                      <div className="labelOne">{detailData.amountIn}</div>
                    </FormGroup>
                  </Col>
                  <Col className="pr-md-1" md="12">
                    <FormGroup>
                      <label>Current Price</label>
                      <div className="labelOne">{detailData.amountOut}</div>
                    </FormGroup>
                  </Col>
                  <Col className="pr-md-1" md="12">
                    <FormGroup>
                      <label>Gas Price</label>
                      <div className="labelOne">{detailData.gasPrice}</div>
                    </FormGroup>
                  </Col>
                  <Col className="pr-md-1" md="12">
                    <FormGroup>
                      <label>Profit</label>
                      <div className="labelOne">{detailData.profit}%</div>
                    </FormGroup>
                  </Col>
                </Row>
              </Col>
              <Col className="pr-md-1" md="6">
              {/* // 0-buying,1-bought,2-buy failed,4-moving,5-moved,6-move failed,7-selling,8-sold,9-sell failed */}
                  <Col className="pr-md-1" md="12"><Button color="warning" size="sm" className="btnOne" onClick={()=>del(detailData._id)}>Delete</Button></Col>
                  <Col className="pr-md-1" md="12"><Button color="default" size="sm" className="btnOne"><a className="text-white" href={`https://bscscan.com/tx/${detailData.tx}`} target="_blank">Transaction</a></Button></Col>
              </Col>
            </Row>

          </div>
        }
      </Modal>
    </>
  );
};
const mapStateToProps = (state) => {
  const { LoginReducer } = state;
  return { credential: LoginReducer };
};
export default connect(mapStateToProps)(UniSwapOneToken);
